import { useState } from 'react';
import './CostForm.css';

const CostForm = (props) => {
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState('');
  const [date, setDate] = useState('');
  const [category, setCategory] = useState('');

  const popularCategories = ['Food', 'Transport', 'Entertainment', 'Utilities'];

  const submitHandler = (event) => {
    event.preventDefault();

    // Проверка: если дата не указана, показываем алерт и прерываем отправку
    if (!date) {
      alert('Please select a valid date.');
      return;
    }

    // Проверка: цена должна быть больше нуля
    if (price <= 0) {
      alert('Price must be greater than zero.');
      return;
    }

    const costData = {
      description,
      price: parseFloat(price),
      date: new Date(date),
      category,
    };

    props.onSaveCostData(costData); // Передаём данные в родительский компонент

    // Сбрасываем значения полей после успешного добавления
    setDescription('');
    setPrice('');
    setDate('');
    setCategory('');
  };

  return (
    <form onSubmit={submitHandler}>
      <div className="new-cost__controls">
        <div className="new-cost__control">
          <label>Name</label>
          <input
            type="text"
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
        </div>
        <div className="new-cost__control">
          <label>Price</label>
          <input
            type="number"
            min="0.01"
            step="0.01"
            value={price}
            onChange={(e) => setPrice(e.target.value)}
          />
        </div>
        <div className="new-cost__control">
          <label>Date</label>
          <input
            type="date"
            min="2022-01-01"
            max="2025-12-31"
            value={date}
            onChange={(e) => setDate(e.target.value)}
          />
        </div>
        <div className="new-cost__control">
          <label>Category</label>
          <select
            value={category}
            onChange={(e) => setCategory(e.target.value)}
          >
            <option value="">Select a category</option>
            {popularCategories.map((cat) => (
              <option key={cat} value={cat}>
                {cat}
              </option>
            ))}
          </select>
          <input
            type="text"
            placeholder="Or enter a custom category"
            value={category}
            onChange={(e) => setCategory(e.target.value)}
          />
        </div>
        <div className="new-cost__actions">
          <button type="submit">Add consumption</button>
          <button type="button" onClick={props.onCancel}>
            Cancel
          </button>
        </div>
      </div>
    </form>
  );
};

export default CostForm;
