import React from "react";
import "./Header.css";

const Header = ({ user, onLogout }) => {
  const currentDate = new Date().toLocaleDateString("en-US", {
    weekday: "long",
    month: "long",
    day: "numeric",
  });

  // Extract the first letter of the user's email or use 'G' for Guest
  const userInitial = user?.email ? user.email.charAt(0).toUpperCase() : "G";
  const userEmail = user?.email || "Guest";

  return (
    <header className="app-header">
      <div className="header-content">
        <div className="header-left">
          <h1>Expense Tracker</h1>
          <p className="header-date">{currentDate}</p>
        </div>
        <div className="header-right">
          <div className="user-avatar" title={userEmail}>
            {userInitial}
          </div>
          <button onClick={onLogout} className="logout-button">
            Logout
          </button>
        </div>
      </div>
    </header>
  );
};

export default Header;
